<template>
  <div class="row btn_row">
    <PurpleButton text="Import" :on-click="wE(this.import)"/>
    <PurpleButton text="Create" :on-click="this.openCreateTokenAlert"/>
  </div>
  <div class="hr grey_back btn_row" style="margin-top: 10px; margin-bottom: 10px;"></div>
  <button type="button" v-for="index in tokens.length" :key="index" class="rounded-3 pool_back container1 token_bar" style="justify-self: center; padding-top: 5px; padding-bottom: 5px;"
  v-on:click="activeToken = (index-1)" :disabled="activeToken===(index-1)">
    <DisplayName :symbol="tokens[index-1].symbol" :value="tokens[index-1].address" :first-text="true"/>
    <div class="hr grey_back inner_row" style="margin-left: 20px; margin-right: 20px;"/>
    <DisplayRow symbol="Mint" :value="tokens[index-1].address"/>
    <DisplayRow symbol="Owner" :value="owners[index-1]? owners[index-1]: '_'"/>
    <DisplayRow symbol="Supply" :value="displayFloat(supply[index-1]? supply[index-1]: -1, decimals[index-1]? decimals[index-1]: 0)" :is-white="true"/>
    <div class="row">
      <div></div>
      <PurpleButton text="Mint" :on-click="wE(mint)" :disabled="owners[index-1]!==wallet" v-if="activeToken===(index-1)&&(owners[index-1])"/>
    </div>
  </button>

  <CreateTokenAlert :w-e="wE" :log="log" :show="show" :sign-tx="signTx" :post="post" id="create-token-alert"
                    :wallet="wallet" :open="openCreateTokenAlert" :add-token="addToken"/>

</template>

<script>
import PurpleButton from "@/elements/PurpleButton";
import DisplayName from "@/elements/DisplayName";
import DisplayRow from "@/elements/DisplayRow";
import {checkMint, mintTx, parseFloat, parseMint, parsePubkey, displayFloat, parseMetadata, getMetadataAddress} from "@/methods";
import {PublicKey} from "@solana/web3.js";
import Swal from "sweetalert2";
import CreateTokenAlert from "@/pages/CreateTokenAlert";

export default {
  name: "CustomTokens",
  components: {CreateTokenAlert, DisplayRow, DisplayName, PurpleButton},
  props: ["wE", 'wallet', 'post', 'signTx', 'show', 'log'],
  methods:{
    async import(){
      await Swal.fire({
        title: "Import Token Mint",
        background: "#0E0F28",
        color: "#d9d9d9",
        html:
        '<input id="swal-input1" class="swal2-input" style="text-align: end; width: 350px; color: #7040ea" placeholder="Symbol">' +
        '<input id="swal-input2" class="swal2-input" style="text-align: end; width: 350px; color: #68bce0" placeholder="address">',
        inputPlaceholder: 'address',
        inputAttributes: {
          autocapitalize: 'off',
        },
        confirmButtonColor: "#2ED69B",
        cancelButtonColor: "#d96868",
        showCancelButton: true,
        showLoaderOnConfirm: true,
        preConfirm: async () => {
          let symbol = document.getElementById("swal-input1").value;
          const amount = document.getElementById("swal-input2").value;
          await this.wE(async () => {
            const mint = parsePubkey(amount);
            await checkMint(mint);
            const mint_a = mint.toBase58();
            this.tokens.forEach(t => {
              if(t.address === mint_a){
                throw "Token Already Imported";
              }
            });
            const meta_a = await getMetadataAddress(mint);
            const meta = await parseMetadata(meta_a);
            if(meta.exist){
              symbol = meta.data.symbol;
              this.addToken(mint_a, symbol);
            }
            else{
              if(symbol==="") throw "No Symbol Found";
              this.addToken(mint_a, symbol);
            }
          })()
        }
      });
    },
    create(){

    },
    async mint(){
      await Swal.fire({
        title: this.tokens[this.activeToken].symbol,
        background: "#0E0F28",
        color: "#d9d9d9",
        input: 'text',
        inputPlaceholder: 'amount',
        inputAttributes: {
          autocapitalize: 'off',
        },
        confirmButtonColor: "#2ED69B",
        cancelButtonColor: "#d96868",
        showCancelButton: true,
        confirmButtonText: 'Mint',
        showLoaderOnConfirm: true,
        preConfirm: async (amount) => {
          await this.wE(async () => {
            amount = parseFloat(amount, this.decimals[this.activeToken]);
            const tx = await mintTx(this.wallet, this.tokens[this.activeToken].address, amount);
            if(await this.signTx(tx, [], this.log)){
              this.refresh();
            }
          })()
        }
      });
    },
    async addToken(mint, symbol){
      this.tokens.push({address: mint, symbol: symbol});
      if(await this.post({list: this.tokens}, "/set_tokens")){
        this.refresh();
        console.log("Added:", mint, symbol);
      }
    },
    async loadTokens(){
      const o = await this.post({}, "/get_tokens");
      if(!o) return;
      this.tokens = o.list;
    },
    async refresh(){
      if(!this.tokens) return;
      this.decimals = [];
      this.supply = [];
      this.owners = [];
      for(let i = 0; i < this.tokens.length; i++){
        try{
          const mint = await parseMint(new PublicKey(this.tokens[i].address));
          this.decimals.push(mint.decimals);
          this.supply.push(mint.supply.toString());
          this.owners.push(mint.mintAuthority.toBase58());
        }
        catch(e){
          this.decimals.push(undefined);
          this.supply.push(undefined);
          this.owners.push(undefined);
        }
      }
    },
    openCreateTokenAlert(not){
      if(not){
        document.getElementById("create-token-alert").style.display = "none";

      }else{
        document.getElementById("create-token-alert").style.display = "grid";
      }
    }
  },
  async mounted() {
    await this.loadTokens();
    await this.refresh();
  },
  data(){
    return{
      tokens: [],
      decimals: [],
      supply: [],
      owners: [],
      activeToken: -1,
      displayFloat: displayFloat
    }
  }
}
</script>

<style scoped>
.swal2-input{
  text-align: end;
  width: 500px;
}
.inner_row{
  margin-top: 0px;
  margin-bottom: 0px;
}
.token_bar{
  background-color: #0E0F28;
  border: 1px solid #010207;
}
.token_bar:hover{
  background-color: #0E0F28;
  border: 1px solid #525252;
}
.token_bar:active{
  background-color: #0E0F28;
  border: 1px solid #d9d9d9;
}
.token_bar:disabled{
  background-color: #0E0F28;
  border: 1px solid #d9d9d9;
}
</style>