<template>
  <div class="navbar-expand pool_back" id="my_navbar" style="position: fixed">
    <div class="container2">
      <div class="navbar">
        <img src="@/Engine_Logo.png" class="logo_img"/>
        <div class="nav-item centered" style="padding: 0">
          <button style="border: none; background-color: transparent">
            <h3 class="logo purple_letter">Stake Engine</h3>
          </button>
        </div>
        <NavTitle :active="active" :on_click="on_click" :index="1" text="Instance"/>
        <NavTitle :active="active" :on_click="on_click" :index="2" text="Rewards"/>
        <NavTitle :active="active" :on_click="on_click" :index="3" text="Loader"/>
        <NavTitle :active="active" :on_click="on_click" :index="5" text="Tokens"/>
        <NavTitle :active="active" :on_click="on_click" :index="4" text="Engine"/>
        <div class="nav-item centered wallet_button">
          <wallet-multi-button dark></wallet-multi-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { WalletMultiButton } from 'solana-wallets-vue';
import NavTitle from "@/elements/NavTitle";
export default {
  name: "NavBar",
  props: ['active', 'on_click', 'config'],
  components: {
    NavTitle,
    WalletMultiButton
  },
}

new ResizeObserver(onResized).observe(document.getElementById('size_container'))
function onResized(){
  const new_width = document.getElementById('size_container').clientWidth;
  if(new_width < 1450){
    const ratio = new_width / 1450;
    document.getElementById('my_navbar').style.zoom = Math.ceil(ratio*100)+'%';
  }
  else{
    document.getElementById('my_navbar').style.zoom = '100%';
  }
}
</script>

<style>
#app .swv-button-trigger p {
  margin-top: 15px;
}
</style>

<style scoped>
.navbar{
  min-width: 1250px;
  width: calc(100vw - 1250px);
  margin-left: max(100px, calc(calc(100vw - 1250px) / 2));
  margin-right: max(100px, calc(calc(100vw - 1250px) / 2));
}
.navbar-expand{
  margin-bottom: 20px;
  min-width: 1350px;
  top: 0;
}
.container2{
  height: 80px;
}
.nav-item{
  display: inline-block;
  padding: 10px;
}
.centered{
  align-items: center;
}
.logo{
  color: #FFFDFD;
  margin-left: 110px;
}
.logo_img{
  position: absolute;
  height: 75px;
  width: 112.5px;
}
</style>