<template>
  <div class="blackout">
    <div class="container3 container rounded-4 pool_back">
      <div class="row">
        <div></div>
        <h4 class="white_letter">Create Custom Token</h4>
        <div></div>
      </div>
      <DisplayRow symbol="Owner" :value="wallet" :underline="true"/>
      <div class="row">
        <div class="white_letter text">Name</div>
        <input class="input blue_letter"
               :value="name"
               @input="event => this.name = event.target.value"
               placeholder="Name" aria-label="Name" aria-describedby="basic-addon3">
      </div>
      <div class="row">
        <div class="white_letter text">Symbol</div>
        <input class="input blue_letter"
               :value="symbol"
               @input="event => this.symbol= event.target.value"
               placeholder="Symbol" aria-label="Symbol" aria-describedby="basic-addon3">
      </div>
      <div class="row">
        <div class="white_letter text">Decimals</div>
        <input class="input blue_letter"
               :value="decimals"
               @input="event => this.decimals = event.target.value"
               placeholder="decimals" aria-label="Decimals" aria-describedby="basic-addon3">
      </div>
      <div class="row">
        <div class="white_letter text">Metadata Uri</div>
        <input class="input blue_letter"
               :value="meta_uri"
               @input="event => this.meta_uri = event.target.value"
               placeholder="uri" aria-label="Meta URI" aria-describedby="basic-addon3">
      </div>
      <div class="row btn_row" style="margin-top: 50px; margin-bottom: 10px">
        <BlueButton text="Create" :on-click="wE(create)" color="success_button"/>
        <BlueButton text="Cancel" :on-click="this.back" color="error_button"/>
      </div>
    </div>
  </div>

</template>

<script>
import DisplayRow from "@/elements/DisplayRow";
import BlueButton from "@/elements/BlueButton";
import {createTokenTx} from "@/methods";
export default {
  name: "CreateTokenAlert",
  components: {BlueButton, DisplayRow},
  props: ['wE', 'signTx', 'log', 'show', 'open', 'post', 'wallet', 'addToken'],
  methods: {
    back(){
      this.open(true);
    },
    async create(){
      if(!this.wallet) throw "Connect wallet first";
      if(this.name === "") throw "Empty name";
      if(this.symbol === "") throw "Empty symbol";
      //if(this.meta_uri === "") throw "Empty uri";
      const decimals = parseInt(this.decimals);
      const [tx, mint] = await createTokenTx(this.wallet, this.name, this.symbol, this.meta_uri, decimals);
      if(await this.signTx(tx, [mint], this.log)){
        if(await this.addToken(mint.publicKey.toBase58(), this.symbol)){
          this.show(true, "Successfully created");
        }
      }
      this.back();
    }
  },
  data(){
    return{
      symbol: "",
      name: "",
      decimals: 9,
      meta_uri: ""
    }
  }
}
</script>

<style scoped>
.blackout {
  position: absolute;
  width: 100vw;
  height: 100vh;
  top: 0; left: 0;
  background: rgba(0,0,0,.5);
  z-index: 5;
  display: none;
}
.container3{
  max-width: 600px;
  width: 750%;
  min-height: 0px;
  display: inline-grid;
  justify-self: center;
  padding-bottom: 20px;
  padding-top: 20px;
  justify-content: center;
  position: fixed;
  margin-top: 10%;
}
.input{
  background: transparent;
  margin-right: 10px;
  width: 225px;
  padding: 0px;
  border-top-color: transparent;
  border-right-color: transparent;
  border-left-color: transparent;
  border-bottom: transparent;
  text-align: right;
}
.input::placeholder {
  text-align: right;
}
.selector{
  width: 400px;
  margin-right: 0px;
  margin-left: 20px;
  border-color: transparent;
  background-color: transparent;
  text-align: center;
  outline: 2px solid #d0d0d0;
  border-radius: 5px;
}
.form-select option{
  border: none;
}
.pale_letter{
  justify-self: center;
  width: 38px;
  height: 38px;
  margin-top: 0;
  margin-bottom: 0;
  margin-right: 37px;
  margin-left: 37px;
}
</style>