<template>
  <div class="row btn_row">
    <PurpleButton text="Back" :on-click="wE(this.back)"/>
    <BlueButton text="Flush" :on-click="wE(this.flush)"/>
  </div>
  <div class="container1 container rounded-4 pool_back">
    <DisplayRow symbol="Owner" :value="wallet" :underline="true"/>
    <div class="row">
      <div class="white_letter text">Collection</div>
      <input class="input blue_letter"
             :value="this.collection"
             @input="event => this.collection = event.target.value"
             placeholder="collection address" aria-label="collection address" aria-describedby="basic-addon3">
    </div>
    <div class="hr grey_back"/>
    <DisplayRow symbol="Loader" :value="loader" :underline="true"/>
    <div class="row">
      <div class="white_letter text">Specification</div>
      <select class="form-select selector white_hover" aria-label="Specification chooser" ref="spec-selector"
              @input="event => this.spec = event.target.value">
        <option hidden>_</option>
        <option value=0>Stake</option>
        <option value=1>Lock</option>
      </select>
    </div>
    <TimeInput title="Unit Time" underline="true" ref="time-input" :w-e="wE"/>
    <div class="row" style="margin-top: 5px">
      <h5 class="purple_letter">Reward Tokens</h5>
      <select class="form-select selector white_hover" aria-label="add reward token"
              @input="event => {this.addToken(event.target.value); event.target.value = 'Add'}">
        <option hidden>Add</option>
        <option value="0">Token</option>
        <option value="1">SOL</option>
        <option value="2">USDT</option>
        <option value="3">USDC</option>
        <option value="4">BUSD</option>
        <option v-for="index in custom_symbols.length" :key="index" :value="index+4">{{custom_symbols[index-1]}}</option>
      </select>
    </div>
    <div v-for="index in tokens.length" :key="index">
      <div class="row">
        <span class="error_hover" v-on:click="remToken(index)"
              style="font-size: 15px;">&#x2715;</span>
        <input class="input blue_letter"
               :value="this.tokens[index-1]"
               @input="event => this.tokens[index-1] = event.target.value"
               placeholder="token address" aria-label="token address" aria-describedby="basic-addon3">
      </div>
    </div>
  </div>
  <PurpleButton text="Configure" :on-click="wE(this.configure)"/>
</template>

<script>
import PurpleButton from "@/elements/PurpleButton";
import TimeInput from "@/elements/TimeInput";
import DisplayRow from "@/elements/DisplayRow";
import {useLocalStorage} from "@vueuse/core/index";
import BlueButton from "@/elements/BlueButton";
import {checkCollection, parsePubkey, parseTokens} from "@/methods";
export default {
  name: "InstanceInit",
  props: ["wE", 'wallet', 'post', 'postPageState', 'isRunning', 'show', 'config', 'setConfig'],
  components: {
    BlueButton,
    PurpleButton,
    TimeInput,
    DisplayRow
  },
  async mounted() {
    this.loadTokens();
    if(this.spec){
      this.$refs["spec-selector"].value = this.spec;
    }
    else{
      this.$refs["spec-selector"].selectedIndex = null;
    }
    const answer = await this.post({}, "/get_loader");
    this.loader = answer? answer.loader: "none";
  },
  methods: {
    async loadTokens(){
      const o = await this.post({}, "/get_tokens");
      if(!o) return;
      this.custom_tokens.clear();
      this.custom_symbols = [];
      o.list.forEach(p => {
        this.custom_tokens.set(p.symbol, p.address);
        this.custom_symbols.push(p.symbol);
      });
    },
    flush(){
      this.collection = "";
      this.tokens = [];
      this.$refs["spec-selector"].selectedIndex = null;
      this.spec = null;
      this.$refs["time-input"].flush();
    },
    async back(){
      await this.postPageState("none", "instance");
    },
    async configure(){
      let _config = this.config;
      _config.owner = parsePubkey(this.wallet).toBase58();
      _config.collection = await checkCollection(parsePubkey(this.collection));
      _config.loader = parsePubkey(this.loader).toBase58();
      if(this.spec != 0 && this.spec != 1) throw "Choose specification";
      _config.spec = parseInt(this.spec);
      if(this.tokens.length === 0) throw "Choose at lest one reward token";
      for(let i = 0; i < this.tokens.length; i++){
        if(this.custom_tokens.has(this.tokens[i])){
          this.tokens[i] = this.custom_tokens.get(this.tokens[i]);
        }
      }
      [_config.tokens, _config.decimals] = await parseTokens(this.tokens);
      _config.unit_time = (await this.$refs["time-input"].gatherDate()).toString();
      if((await this.post(_config, "/configure_instance"))){
        await this.postPageState("configured", "instance");
      }
    },
    remToken(index){
      this.tokens.splice(index-1, 1);
    },
    addToken(token){
      if(this.tokens.length >= 5){
        this.show(false, "No more than 5 tokens available");
        return;
      }
      switch(token) {
        case '0':
          this.tokens.push("");
          return;
        case '1':
          if(this.tokens.includes('SOL')){
            this.show(false, "Already included");
            return;
          }
          this.tokens.push('SOL');
          break;
        case '2':
          if(this.tokens.includes('USDT')){
            this.show(false, "Already included");
            return;
          }
          this.tokens.push('USDT');
          break;
        case '3':
          if(this.tokens.includes('USDC')){
            this.show(false, "Already included");
            return;
          }
          this.tokens.push('USDC');
          break;
        case '4':
          if(this.tokens.includes('BUSD')){
            this.show(false, "Already included");
            return;
          }
          this.tokens.push('BUSD');
          break;
        default: {
          token -= 5;
          if(this.custom_symbols.length > token){
            if(this.tokens.includes(this.custom_symbols[token])){
              this.show(false, "Already included");
              return;
            }
            this.tokens.push(this.custom_symbols[token]);
          }
          else{
            this.tokens.push("");
          }
        }
      }
    },
  },
  data(){
    return{
      collection: useLocalStorage('inputCollection', ""),
      loader: useLocalStorage('loaderPubkey', "none"),
      spec: useLocalStorage('inputSpec', null),
      tokens: useLocalStorage('inputTokens', []),
      custom_tokens: new Map(),
      custom_symbols: []
    }
  },
}
</script>

<style scoped>
.input{
  background: transparent;
  margin-right: 10px;
  width: 425px;
  padding: 0px;
  border-top-color: transparent;
  border-right-color: transparent;
  border-left-color: transparent;
  border-bottom: transparent;
  text-align: right;
}
.input::placeholder {
  text-align: right;
}
.text{
  padding: 0px 10px 0px 10px;
}
.row{
  display: grid;
  grid-auto-flow: column;
  padding-left: 20px;
  padding-right: 20px;
  margin-top: 10px;
  justify-content: space-between;
}
.selector{
  border-color: transparent;
  background-color: transparent;
  text-align: right;
}
.form-select option{
  border: none;
}
</style>